@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgba(56, 152, 255,1) 0%, rgba(26, 25, 25,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* #### Generated By: http://www.cufonfonts.com #### */

@font-face {
  font-family: 'SEGA LOGO FONT';
  font-style: normal;
  font-weight: normal;
  src: local('SEGA LOGO FONT'), url(/robofriends/static/media/SEGA.47b4bfa9.woff) format('woff');
}

h1 {
  font-family: 'SEGA LOGO FONT', 'Major Mono Display', monospace;
  font-weight: 200;
  color: #0ccac4;
}

